<template>
<div class="works-page main grid-wrap">

  <!-- 列表页 -->
  <router-view @listTap="listTap" :Listdata="articleData"></router-view>
  <!-- <portfolioList :Listdata="articleData"></portfolioList> -->
  <!-- 详情页 -->

	</div> <!--main-->
</template>
<script>
import portfolioList from '../protfolio-list/index'
export default {
  name: "portfolio",
  data: function () {
    return {
      articleData: '',
      articleId: ''
    }
  },
  created() {
    this.$http.post(ajax_port+"/public/portfolio/getPortfolio").then(response => {
      response = response.data;
      if(response.success == true){
        console.log(response)
        this.articleData = response.data;
      }
    });
  },
  methods:{
    listTap: function(id){
      var data = selectData(this.articleData,id);
      this.$router.push({ name: 'Detail', params:{'articleData':data}})
    }
  },
  components: {
    portfolioList
  }
}

function selectData(Arr,id){
  for(var i=0;i<Arr.length;i++){
    if(Arr[i]._id == id){
      return Arr[i];
    }
  }
}
</script>



