  </div>
</template>
<script>
import blogList from '../blog-list/index'
export default {
  name: "blog",
  data: function () {
    return {
      blogData: '',
      blogId: ''
    }
  },
  components: {
    blogList
  },
   created() {
    this.$http.post(ajax_port+"/public/blog/getBlog").then(response => {
      response = response.data;
      if(response.success == true){
        console.log(response)
        this.blogData = response.data;
      }
    });
  },
  methods:{
    blogTap: function(id){
      var data = selectData(this.blogData,id);
      this.$router.push({ name: 'BlogDetail', params:{'blogData':data}})
    }
  }
}

function selectData(Arr,id){
  for(var i=0;i<Arr.length;i++){
    if(Arr[i]._id == id){
      return Arr[i];
    }
  }
}
</script>




