<template>
  <div>
    <!-- 侧边栏 -->
		<aside class="grid col-one-quarter mq2-col-full">
        <p class="mbottom">作品集XXXXX我我发顺丰萨福克拉法基阿发解放路费</p>
        <menu>
            <a  id="work_all" class="arrow buttonactive">All</a><br>
            <a  id="work_1" class="arrow">Android design</a><br>
            <a  id="work_2" class="arrow">Web design</a><br>
            <a  id="work_2" class="arrow">Python design</a><br>
            <a  id="work_2" class="arrow">Games design</a><br>
            <a  id="work_2" class="arrow">UI design</a><br>
        </menu>
	</aside>
    <!-- 博文列表 -->
    <section class="grid col-three-quarters mq2-col-full">
        <div class="grid-wrap works">
          <articleList @articleId = "articleId" v-for=" item in Listdata" :key="item._id" :cover='item.cover' :title="item.title" :intro="item.intro" :id="item._id"></articleList>
        </div> <!-- grid inside 3/4-->
    </section>
    <ul class="page-numbers">
      <li><a href="#">Prev</a></li>
      <li><a href="#" class="current">1</a></li>
      <li><a href="#">2</a></li>
      <li><a href="#">3</a></li>
      <li><a href="#">4</a></li>
      <li><a href="#">Next</a></li>
    </ul>
  </div>
</template>

<script>
import articleList from "../article/index";
export default {
  name: "portfolio-list",
  props:{
    Listdata: {
      type: Array
    }
  },
  data: function () {
    return {
      // articleData: ''
    }
  },
  methods:{
    // 再次向上传递，传递到父组件(触发路由改变)
    articleId: function(id){
      this.$emit('listTap',id);
    }
  },
  components: {
    articleList
  }
}
</script>



