import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import blog from '@/components/blog'
import portfolio from '@/components/portfolio'
import download from '@/components/download'
import portfolioList from '@/components/protfolio-list'
import portfolioDetail from '@/components/protfolio-detail'
import blogList from '@/components/blog-list'
import blogDetail from '@/components/blog-detail'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/blog',
      name: 'blog',
      component: blog,
      redirect: '/blog/list',
      children: [
        {
          path: 'list',
          name: 'blogList',
          component: blogList
        },
        {
          path: 'blogDetail',
          name: 'BlogDetail',
          component: blogDetail
        }
      ]
    },
    {
      path: '/portfolio',
      name: 'portfolio',
      component: portfolio,
      redirect: '/portfolio/list',
      children: [
        {
          path: 'list',
          name: 'portfolioList',
          component: portfolioList
        },
        {
          path: 'detail',
          name: 'Detail',
          component: portfolioDetail
        }
      ]
    },
    {
      path: '/download',
      name: 'download',
      component: download
    }
  ]
})



